import React from 'react'
import { Route } from 'react-router-dom'
import { Container, Nav, Navbar, NavDropdown } from 'react-bootstrap'
import { LinkContainer } from 'react-router-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import { logout } from '../actions/userActions'
import SearchBox from './SearchBox'

function Header() {
    const dispatch = useDispatch()

    const userLogin = useSelector(state => state.userLogin)
    const {  userInfo } = userLogin

    const logoutHandler = () => {
        dispatch(logout())
    }

    return (
        <header>
            <Navbar bg="success" variant="light" expand="lg" collapseOnSelect>
                <Container>
                <LinkContainer to="/">
                    <Navbar.Brand>Pro-Shop</Navbar.Brand>
                </LinkContainer>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Route render={({ history }) => <SearchBox history={history} />}  />
                    <Nav className="ml-auto">
                    <LinkContainer style={{color: 'black'}} to="/cart">
                        <Nav.Link>
                            <i className="fas fa-shopping-cart pr-1"></i> Cart
                        </Nav.Link>
                    </LinkContainer>
                    {userInfo ? (
                        <NavDropdown title={userInfo.name} id='username'>
                            <LinkContainer to="/profile">
                                <NavDropdown.Item>Profile</NavDropdown.Item>
                            </LinkContainer>
                            <NavDropdown.Item onClick={logoutHandler}>Logout</NavDropdown.Item>

                        </NavDropdown>
                    ): <LinkContainer style={{color: 'black'}} to="/login">
                        <Nav.Link>
                            <i className="fas fa-user pr-1"></i> Sign In
                        </Nav.Link>
                    </LinkContainer>
                    }
                    {userInfo && userInfo.isAdmin && (
                        <NavDropdown title="Admin" id='adminmenu'>
                            <LinkContainer to="/admin/userlist">
                                <NavDropdown.Item>Users</NavDropdown.Item>
                            </LinkContainer>
                            <LinkContainer to="/admin/productlist">
                                <NavDropdown.Item>Products</NavDropdown.Item>
                            </LinkContainer>
                            <LinkContainer to="/admin/orderlist">
                                <NavDropdown.Item>Orders</NavDropdown.Item>
                            </LinkContainer>
                                               
                        </NavDropdown>
                    )}
                    </Nav>
                </Navbar.Collapse>
                </Container>
            </Navbar>
        </header>
    )
}

export default Header
